import React, { useEffect, useState, useCallback } from "react";
import Head from 'next/head'
import { useSelector } from "react-redux";
import Router from 'next/router';
import { END } from 'redux-saga';
import axios from 'axios';
import useSWR from 'swr';

import AppLayout from '../components/AppLayout';
import NicknameEditForm from '../components/NicknameEditForm';
import FollowList from '../components/FollowList';
import { LOAD_MY_INFO_REQUEST } from '../reducers/user';
import wrapper from "../store/configureStore";
import { LOAD_POSTS_REQUEST } from "../reducers/post";

const fetcher = (url) => axios.get(url, { withCredentials: true }).then((result) => result.data);
 
const Profile = () => {
    const {me} = useSelector((state) => state.user);
    const [followersLimit, setFollowersLimit] = useState(3);
    const [followingsLimit, setFollowingsLimit] = useState(3);

    const {data : followersData, error : followersError} = useSWR(`http://localhost:3065/user/followers?limit=${followersLimit}`, fetcher);
    const {data : followingsData, error : followingsError} = useSWR(`http://localhost:3065/user/followings?limit=${followingsLimit}`, fetcher);

    useEffect(() => {
      if(!(me && me.id)) {
        Router.push('/');
      }
    },[me && me.id]);

    const loadMoreFollowings = useCallback(() => {
      setFollowingsLimit((prev) => prev + 3);
    },[]);

    const loadMoreFollowers = useCallback(() => {
      setFollowersLimit((prev) => prev + 3);
    },[]);

    if(!me) {
      return '  ...';
    }

    // return useEffect hooks    .        
    if(followersError || followingsError) {
      console.error(followersError || followingsError);
      return <div>`/    .`</div>;
    }

    return(
      <>
        <Head>
          <title>  ! Profile</title>
        </Head>
        <AppLayout>
          <NicknameEditForm />
          <FollowList header="" data={followingsData} onClickMore={loadMoreFollowings} loading={!followingsData && !followingsError} />
          <FollowList header="" data={followersData} onClickMore={loadMoreFollowers} loading={!followersData && !followersError} />
        </AppLayout>
      </>
    );
}

export const getServerSideProps = wrapper.getServerSideProps(async (context) => {
  const cookie = context.req ? context.req.headers.cookie : '';
  axios.defaults.headers.Cookie = '';
  if (context.req && cookie) {
    axios.defaults.headers.Cookie = cookie;
  }
  context.store.dispatch({
    type: LOAD_MY_INFO_REQUEST,
  });
  context.store.dispatch({
    type: LOAD_POSTS_REQUEST,
  });
  context.store.dispatch(END);
  await context.store.sagaTask.toPromise();
});

export default Profile;